@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  min-width: 900px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Quicksand";
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
}

a {
  color: black;
  text-decoration: none;
}

a:link {
  color: black;
  color: rgb(127, 118, 255);
}

/* visited link */
a:visited {
  color: black;
}

/* mouse over link */
a:hover {
  color: black;
}

/* selected link */
a:active {
  color: black;
}

#navbar1 {
  width: 100vw;
  min-width: 900px;
  height: 80px;
  background-color: rgb(52, 73, 94);
  z-index: 1;
  font-size: 22px;
  color: aliceblue;
  display: grid;
  grid-template-columns: 15% 15% 28% 27% 15%;
}

#newarr {
  padding: 0 auto;
}

#newarr:hover {
  background-color: rgba(87, 118, 185, 0.794);
}

#sorts_sec {
  z-index: 3;
}

#sort_btn {
  margin: 20px 10px;
  margin-top: 16px;
  margin-left: 20px;
  padding: 8px 16px;
  background-color: rgb(90, 122, 190);
  border-radius: 3px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
}

#sort_btn:hover {
  background-color: rgba(87, 118, 185, 0.794);
}

#logo_div {
  margin: 5px auto;
  width: 200px;
  height: 70px;
  cursor: pointer;
  z-index: 5;
}

#rangebar {
  display: inline-flex;
  margin: 0 20px;
  margin-top: 25px;
  width: 440px;
}

#rangebar #lb1 {
  padding-right: 15px;
}

#rangebar #lb2 {
  padding: 0 15px;
  position: absolute;
  margin-left: 380px;
}

#rangebar input {
  position: absolute;
  margin-left: 180px;
  cursor: pointer;
  background-color: antiquewhite;
  width: 200px;
  margin-bottom: 30px;
  margin-top: 5px;
}

#newarr {
  margin: 20px 20px;
  padding: 5px 20px;
  border-radius: 3px;
  background-color: rgb(90, 122, 190);
  cursor: pointer;
  font-weight: 500;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
#selctd {
  margin: 0 auto;
  margin-top: 20px;
  width: 300px;
  height: 40px;
  background-color: rgb(255, 255, 255);
  cursor: pointer;
  border-radius: 3px;
}

#opt_box {
  margin: 0 auto;
  background-color: rgb(52, 73, 94);
  width: 300px;
  border-radius: 5px;
  margin-top: 10px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.657);
}

#opt_box p {
  margin: 0;
  padding: 8px 10px;
}

#opt_box p:hover {
  background-color: rgb(165, 186, 231);
  cursor: pointer;
  font-weight: 800;
}

#dropicon {
  color: rgb(46, 1, 46);
  float: right;
  padding: 0 5px;
  margin-top: 10px;
  border-left: 1px solid black;
}

#selctd_srt {
  display: inline-block;
  color: black;
  font-weight: 500;
  padding-left: 10px;
  padding-top: 5px;
}

#blk_div2 {
  height: 100vh;
  width: 100%;
  position: fixed;
  background-color: transparent;
  z-index: 3;
}

@media (max-width: 1480px) {
  #navbar1 {
    font-size: 18px;
  }
  #rangebar input {
    position: absolute;
    margin-left: 140px;
    cursor: pointer;
    background-color: antiquewhite;
    width: 150px;
  }
  #rangebar #lb1 {
    padding-right: 10px;
  }

  #rangebar #lb2 {
    padding: 0 5px;
    position: absolute;
    margin-left: 300px;
  }
  #selctd {
    width: 250px;
    height: 35px;
  }
  #opt_box {
    width: 250px;
  }
  #selctd_srt {
    padding-top: 5px;
  }
  #sort_btn {
    margin-top: 16px;
    margin-left: 5px;
    padding: 8px 8px;
  }
}

@media (max-width: 1160px) {
  #navbar1 {
    grid-template-columns: 15% 15% 30% 25% 15%;
    font-size: 16px;
  }
  #rangebar input {
    cursor: pointer;
    background-color: antiquewhite;
    /* background-color: antiquewhite; */
    width: 100px;
    position: absolute;
    margin-left: 120px;
    margin-top: 2px;
  }
  #newarr {
    font-size: 16px;
    margin: 20px 5px;
    margin-left: 35px;
    padding: 5px 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  #rangebar #lb1 {
    padding-right: 5px;
  }

  #rangebar #lb2 {
    padding: 0 2px;
    position: absolute;
    margin-left: 230px;
  }
  #selctd {
    width: 200px;
    height: 30px;
  }
  #opt_box {
    width: 200px;
  }
  #logo_div {
    margin: 10px auto;
    width: 150px;
    height: 60px;
  }
  #sort_btn {
    margin-top: 16px;
    margin-left: 5px;
    padding: 8px 8px;
  }
}

