@-webkit-keyframes popup3 {
  0% {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    background-color: rgb(255, 135, 135);
    border-radius: 100%;
  }
  25% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    background-color: rgb(255, 217, 48);
    border-radius: 75%;
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    background-color: rgb(110, 255, 195);
    border-radius: 50%;
  }
  75% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(226, 196, 255);
    border-radius: 10%;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(128, 170, 255);
    border-radius: 0%;
  }
}

